import { useState } from 'react';
import { useAuth } from '@/hooks/useAuth';

interface SignupFormProps {
  onClose: () => void;
}

export default function SignupForm({ onClose }: SignupFormProps) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);
  const { signup } = useAuth();

  const validateEmail = (email: string) => {
    if (email.length > 100) {
      return 'Ïù¥Î©îÏùºÏùÄ 100ÏûêÎ•º Ï¥àÍ≥ºÌï† Ïàò ÏóÜÏäµÎãàÎã§';
    }
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      return 'Ïú†Ìö®Ìïú Ïù¥Î©îÏùº Ï£ºÏÜåÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî';
    }
    return null;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    // Ïù¥Î©îÏùº Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
    const emailError = validateEmail(email);
    if (emailError) {
      setError(emailError);
      return;
    }

    try {
      await signup(email, password);
      setSuccess(true);
      setTimeout(() => {
        onClose();
      }, 2000);
    } catch (err: any) {
      if (err.message.includes('Í∞ôÏùÄ ÏïÑÏù¥ÎîîÍ∞Ä Ï°¥Ïû¨Ìï©ÎãàÎã§')) {
        setError('Í∞ôÏùÄ ÏïÑÏù¥ÎîîÍ∞Ä Ï°¥Ïû¨Ìï©ÎãàÎã§');
        setEmail('');
        setPassword('');
      } else if (err.message.includes('100ÏûêÎ•º Ï¥àÍ≥º')) {
        setError('Ïù¥Î©îÏùºÏùÄ 100ÏûêÎ•º Ï¥àÍ≥ºÌï† Ïàò ÏóÜÏäµÎãàÎã§');
      } else {
        setError('ÌöåÏõêÍ∞ÄÏûÖ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§');
      }
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {success ? (
        <div className="text-green-500 text-center py-4">
          ÌöåÏõêÍ∞ÄÏûÖÏù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§!
          <br />
          Î°úÍ∑∏Ïù∏ Ìï¥Ï£ºÏÑ∏Ïöî.
        </div>
      ) : (
        <>
          <div>
            <label className="block text-sm font-medium text-gray-700">Ïù¥Î©îÏùº</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              maxLength={100}
              className="mt-1 block w-full rounded-md border border-gray-300 px-3 py-2 focus:border-blue-500 focus:outline-none"
              required
            />
            <p className="text-xs text-gray-500 mt-1">ÏµúÎåÄ 100Ïûê</p>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="mt-1 block w-full rounded-md border border-gray-300 px-3 py-2 focus:border-blue-500 focus:outline-none"
              required
            />
          </div>
          {error && (
            <div className="text-red-500 text-sm">{error}</div>
          )}
          <button
            type="submit"
            className="w-full bg-blue-500 text-white py-2 px-4 rounded-md hover:bg-blue-600 transition-colors"
          >
            ÌöåÏõêÍ∞ÄÏûÖ
          </button>
        </>
      )}
    </form>
  );
} 