import React, { useState, useEffect, createContext, useContext } from 'react';

interface User {
  id: string;
  name: string;
  email: string;
}

interface AuthContextType {
  user: User | null;
  isAuthenticated: boolean;
  login: (email: string, password: string) => Promise<void>;
  logout: () => void;
  signup: (email: string, password: string) => Promise<void>;
}

const AuthContext = createContext<AuthContextType | null>(null);

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      // Î∞±ÏóîÎìúÏùò ÌÜ†ÌÅ∞ Í≤ÄÏ¶ù API Ìò∏Ï∂ú
      fetch('http://localhost:8000/api/auth/me', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      })
      .then(response => {
        if (response.ok) {
          return response.json();
        }
        throw new Error('Invalid token');
      })
      .then(userData => {
        setUser(userData);
        setIsAuthenticated(true);
      })
      .catch(() => {
        localStorage.removeItem('token');
        setUser(null);
        setIsAuthenticated(false);
      });
    }
  }, []);

  const login = async (email: string, password: string) => {
    try {
      const formData = new FormData();
      formData.append('username', email);  // OAuth2Îäî 'username'ÏùÑ ÏÇ¨Ïö©
      formData.append('password', password);

      const response = await fetch('http://localhost:8000/api/auth/login', {
        method: 'POST',
        body: formData,  // JSON ÎåÄÏã† FormData ÏÇ¨Ïö©
      });

      if (!response.ok) {
        const error = await response.json();
        if (error.detail === "Incorrect email or password") {
          throw new Error('Ïù¥Î©îÏùº ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏûòÎ™ªÎêòÏóàÏäµÎãàÎã§');
        }
        throw new Error('Î°úÍ∑∏Ïù∏ Ïã§Ìå®');
      }

      const data = await response.json();
      localStorage.setItem('token', data.access_token);
      
      // ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
      const userResponse = await fetch('http://localhost:8000/api/auth/me', {
        headers: {
          'Authorization': `Bearer ${data.access_token}`
        }
      });

      if (userResponse.ok) {
        const userData = await userResponse.json();
        setUser(userData);
        setIsAuthenticated(true);
      }
    } catch (error) {
      console.error('Login error:', error);
      throw error;
    }
  };

  const logout = () => {
    localStorage.removeItem('token');
    setUser(null);
    setIsAuthenticated(false);
  };

  const signup = async (email: string, password: string) => {
    // Ïù¥Î©îÏùº Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
    if (email.length > 100) {
      throw new Error('Ïù¥Î©îÏùºÏùÄ 100ÏûêÎ•º Ï¥àÍ≥ºÌï† Ïàò ÏóÜÏäµÎãàÎã§');
    }

    try {
      const response = await fetch('http://localhost:8000/api/auth/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email,
          password,
          is_active: true
        }),
      });

      if (!response.ok) {
        const error = await response.json();
        if (error.detail === "Email already registered") {
          throw new Error('Í∞ôÏùÄ ÏïÑÏù¥ÎîîÍ∞Ä Ï°¥Ïû¨Ìï©ÎãàÎã§');
        }
        throw new Error('ÌöåÏõêÍ∞ÄÏûÖ Ïã§Ìå®');
      }

      return await response.json();
    } catch (error: any) {
      console.error('Signup error:', error);
      throw error;
    }
  };

  return React.createElement(AuthContext.Provider, 
    { value: { user, isAuthenticated, login, logout, signup } },
    children
  );
};

export function useAuth() {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
} 